* {
    box-sizing: border-box;
}

body,
.App {

    font-family: poppins;
    letter-spacing: 0.005em;
    background-color: $clr-bg;
    color: $clr-font;
}

h1,
h2 {
    font-family: poppins-bold;
    letter-spacing: 0.02em;
}

h1 {
    font-size: 3.125rem;
}

h2 {
    font-size: 2.5rem;
}

h3 {
    font-family: poppins-semi;
    font-size: 1.9375rem;
    letter-spacing: 0.01em;

}

h4 {
    font-family: poppins-medium;
    font-size: 1.5625rem;
    letter-spacing: 0.01em;
}

img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}


a {
    font-family: poppins;
    font-size: 0.75em;
    letter-spacing: 0.015em;
    text-decoration: none;
    color: inherit;
}

button {
    cursor: pointer;
    font-family: poppins-medium;
    letter-spacing: 0.02em;
}